.StartPage {
    display: flex;
    align-items: center;
    background-color: #f9f9f9;
    flex: 1 1 auto;

    .start-page__content-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2.8125rem;
        img {
            width: 4rem;
        }
        .description {
            max-width: 13.125rem;
            font-size: 1.375rem;
            line-height: 140%;
            text-align: center;
            color: #808080;
        }
    }
}
